// jQuery EasyPieChart Demo
// =============================================================

const easypiechartDemo = {

  init () {

    this.bindUIActions()

  },

  bindUIActions () {

    // event handlers
    this.handleEasyPieChart()
  },

  colors () {
    return {
      red: '#B76BA3',
      orange: '#EC935E',
      yellow: '#F7C46C',
      green: '#A7C796',
      teal: '#00A28A',
      blue: '#346CB0',
      purple: '#5F4B8B',
      gray: '#BEC1C4',
      grayLight: '#D4D5D7',
      grayLighter: '#F5F5F5'
    }
  },

  getColor (color) {
    return this.colors()[color]
  },

  handleEasyPieChart () {
    const self = this

    $('.easyPieChart').each(function () {
      const $this = $(this)
      let barColor = $this.attr('data-barColor')
      let trackColor = $this.attr('data-trackColor')
      let scaleColor = $this.attr('data-scaleColor')
      let lineWidth = $this.attr('data-lineWidth')
      let size = $this.attr('data-size')
      let rotate = $this.attr('data-rotate')

      // default for undefined
      barColor = barColor || self.getColor('teal')
      trackColor = trackColor || self.getColor('grayLighter')
      scaleColor = scaleColor || self.getColor('grayLight')
      lineWidth = (lineWidth) ? parseInt(lineWidth) : 8
      size = (size) ? parseInt(size) : 120
      rotate = (rotate) ? parseInt(rotate) : 0

      trackColor = (trackColor == 'false' || trackColor == '') ? false : trackColor
      scaleColor = (scaleColor == 'false' || scaleColor == '') ? false : scaleColor

      // initilize easy pie chart
      $this.easyPieChart({
        barColor: barColor,
        trackColor: trackColor,
        scaleColor: scaleColor,
        lineWidth: lineWidth,
        size: size,
        rotate: rotate,
        onStep: function (from, to, currentValue) {
          $(this.el).find('span').text(currentValue.toFixed(0) + '%')
        }
      })
    })
  }
}

easypiechartDemo.init()
